<template>
  <div class="page">
    <div class="content">
      <div class="articles">
        <ul class="list webs">
          <li v-for="(item,xh) in webtype" :key="xh" class="li">
            <div class="title">{{item.typename}}</div>
            <div class="webslist">
              <a class="k" :href="it.url" target="_blank" v-for="(it,ind) in getWebs(item.typename)" :key="ind">
                <div class="ico">{{it.title|first}}</div>
                <div class="text" :title="it.title"><div class="textn"> {{it.title}}</div></div>
              </a>
            </div>
          </li>
        </ul>
      </div>
    </div>
  </div>
</template>
<script>
import {list} from '@/api/api'

export default {
    name: 'articles',
    data() {
        return {
            webtype: [],
            webs: []
        }
    },
    created() {
        let data = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'webtype',
            perpage: 1000

        }
        list(data).then((rs) => {
            this.webtype = rs.data.list
        })
        let data1 = {
            app_key: '1D5AF16FD7D90EE3C5CF70D088249505',
            model_name: 'web',
            perpage: 1000,

        }
        list(data1).then((rs) => {
            this.webs = rs.data.list
        })
    },
    methods: {
        getWebs(typename) {
            let fi = this.webs.filter((el) => {
                return el.typename === typename
            })
            return fi
        }
    },
    computed: {},
    filters: {
        first: (value)=> {
            return value.substring(0,1)
        }

    },
}
</script>

